import React, { Component, Fragment } from 'react';
import { Card, Header, Icon } from 'semantic-ui-react'

import Noticia from './noticia';

class Noticias extends Component {

    


    render() {
        
        return (
            <Fragment>
            <Header as='h2' icon textAlign="center">
                <Icon name='calendar alternate' />
                <Header.Content>
                    Noticias 
                 <Header.Subheader>Actualizamos noticias cada 15 dias</Header.Subheader>
                </Header.Content>
            </Header>
        <Card.Group itemsPerRow={3}>
            <Noticia imagen="/noticias/noticia1.jpg" texto='La persona seleccionada para ser la primera en utilizar AllVision será Ichi, compañera sentimental de Kristan, la cual podra iniciar a acomular stast para su perfil, lo cual permitirá hacer un seguimiento para su implementación en otros usuarios. Ella se encuentra ansiosa por la experiencia, está segura que este proyecto beneficiara a todo el mundo. Menciona: “Este proyecto no solo beneficia  a los grandes empresarios, sino principalmente a las clases bajas ya que su experiencia podrá ser valorada con solo una cámara y evitará que las personas juzguen solo por las apariencias. ”' titulo="La Primera Usuaria" sipnosis="La persona seleccionada para ser la primera en utilizar AllVision será Ichi, compañera sentimental de Kristan"/>
            <Noticia imagen="/noticias/noticia2.jpg" texto='En estados unidos la aceptación es un éxito rotundo, llegando a una votación del 96% de los votantes a favor de la implementación. Kristan CEO de la compañía mencionó en la conferencia tras la rotunda victoria: “Está claro que el mundo confía en nuestra capacidad para recopilar datos y dar el reconocimiento a cada una de las actividades que en realidad valen la pena, les garantizamos que más personas buscarán hacer cosas positivas para sus stast y evitarán cualquier acto delictivo que perjudique su imagen, que puede ser consultada con mucha rapidez, el mundo estará desnudo para todos, las estafas serán imposibles, siempre sabrás con quien estas tratando, sus stast te permitirán saber si es digno de confianza. AllVision hará del mundo un lugar de extrema confianza.”' titulo="EEUU dijo Si" sipnosis="En estados unidos la aceptación es un éxito rotundo, llegando a una votación del 96% de los votantes a favor"/>
            <Noticia imagen="/noticias/noticia3.jpg" texto='Los comités ya están listos, generar los stast de todas las actividades, requiere de mucho esfuerzo, pero ya estamos listos para afrontarlo, para ello se han nombrada más de 1’000.000 comités y incontables participantes en todo el mundo, todos trabajando de forma intensa para generar fuentes de stast justas para todas las profesiones. Los grupos están compuestos por gremios de todos los países y de todas las disciplinas y universidades, para definir profesiones, trabajos , entretenimiento, entre otras muchas fuentes tanto de stast positivos como negativos. En cuanto se vaya consolidando cada una de las profesiones se irán publicando para el público en general.' titulo="Comites trabajando" sipnosis="Los comités ya están listos, generar los stast de todas las actividades, requiere de mucho esfuerzo"/>
            <Noticia imagen="/noticias/noticia4.png" texto='El reconocimiento facial está más que terminado, ya se han realizado pruebas en más de 2’000.000 de imágenes y personas, este reconocimiento se basa más que en el rostro, la altura, parentescos, ubicación y otros,  todo junto permitirá que sea una identificación sin errores. Los ingenieros de AllVision siguen trabajando en métodos seguros y ágiles para garantizar la fluidez y una identificación sin margen de error.' titulo="Ingenieria en AllVision" sipnosis="El reconocimiento facial está más que terminado, ya se han realizado pruebas en más de 2’000.000 de imágenes y personas"/>
            <Noticia imagen="/noticias/noticia5.jpg" texto='Protestas de pequeños grupos de rebeldes, empiezan a manifestarse, las autoridades, mencionan que siempre existen detractores incluso de cambios positivos. Aunque se han presentado disturbios, son menores y no hay de qué preocuparnos, seguramente los stast ayudarán a reducir marchas injustificadas, ya que nadie querrá perjudicar su imagen. Otra de las grandes ventajas de esta nueva tecnología.' titulo="Protestas" sipnosis="Protestas de pequeños grupos de rebeldes, empiezan a manifestarse, las autoridades, mencionan que siempre existen detractores"/>
 
          </Card.Group>
          </Fragment>
            
        );
    }
}

export default Noticias;