import React, { Component } from 'react';
import { Card, Image, Icon, Modal, Header } from 'semantic-ui-react'

class Noticia extends Component {




  render() {
    const { imagen, texto, titulo, sipnosis } = this.props
    return (
      <Card>
        <Image src={imagen} wrapped ui={false} />
        <Card.Content>
          <Card.Header>{titulo}</Card.Header>
          <Card.Meta>
            <span className='date'>1 de Mayo 2040</span>
          </Card.Meta>
          <Card.Description textAlign="left">
            {sipnosis}
          </Card.Description>
        </Card.Content>
        <Card.Content extra>
          <Modal trigger={<a>
            <Icon name='eye' />
            ver mas...
              </a>} closeIcon>
            <Modal.Header>{titulo}</Modal.Header>
            <Modal.Content image>
              <Image wrapped size='huge' src={imagen} />
              <Modal.Description>
                <Header>{titulo}</Header>
                {texto}
              </Modal.Description>
            </Modal.Content>
          </Modal>

        </Card.Content>
      </Card>

    );
  }
}

export default Noticia;