import React, { Component } from 'react';
import { Grid, Image, Button, Checkbox, Message, Form, GridRow } from 'semantic-ui-react'

class FormLogin extends Component {

    state = { name: '', password: '' }


    handleChange = (e, { name, value }) => {
        
      
        this.setState({ [name]: value })
    }


    handleSubmit = () => {
        const { name, password } = this.state
       
        this.props.metodo(true);
        
      }


    render() {
        return (
            <div id="fondo">
            <Grid centered columns={2}>
            <GridRow centered columns={3}>
                <Grid.Column>
                    <Image src='https://img.europapress.es/fotoweb/fotonoticia_20171229171202_640.jpg' size='medium' circular/>
                    
                </Grid.Column>
            </GridRow>
            <GridRow centered columns={3}>
                <Grid.Column>
                    <Form onSubmit={this.handleSubmit}>
                        <Form.Field>
                            <label>Usuario</label>
                            <Form.Input name="name" onChange={this.handleChange} value={this.state.name} placeholder='Ingresa el usuario con el que quieres ser identificado' />
                        </Form.Field>
                        <Form.Field>
                            <label>Contraseña</label>
                            <Form.Input name="password" value={this.state.password} onChange={this.handleChange} placeholder='Identificacion' type="password" />
                        </Form.Field>
                        <Form.Field>
                            <a href="" onClick={()=>{alert("En construcion")}}> ¿Olvido su Contraseña?</a>
                        
                        </Form.Field>
                        <Form.Field>
                            <a href="" onClick={()=>{alert("En construcion")}} > Leer terminos y Condiciones</a>
                        
                        </Form.Field>
                        <Message
      error
      header='Action Forbidden'
      content='You can only sign up for an account once with a given e-mail address.'
    />
                        <Button type='submit'>Ingresar</Button>
                    </Form>
                </Grid.Column>
                </GridRow>
                <GridRow>
                <Button type='submit' onClick={()=>{alert("en construccion utilize cualquier usuario o contraseña")}}>Registrarse</Button>
                </GridRow>
            </Grid>
            </div>
        );
    }
}

export default FormLogin;

