import React, { Component, Fragment } from 'react';
import { Grid, Header, Icon} from 'semantic-ui-react'
import Historia from './historia'
import Historiab from './historiab'
class Historias extends Component {




    render() {
        return (
            <Fragment>
            <Header as='h2' textAlign='center' >
                <Icon name='newspaper outline' />
                <Header.Content>
                    Historia en construccion
                 <Header.Subheader>Fundadores AllVision [1 parte]</Header.Subheader>
                </Header.Content>
            </Header>
            
            <Grid>
                <Grid.Row>
                    <Historia titulo='EL grupo tecnologico InTec' imagen='historia/historia1.jpg' texto='Es el grupo de empresas tecnológicas que se encargan de la gestión de la plataforma con más de 30 empresas asociadas para garantizar que la implementación sea un éxito. ANtes de presentar una propuesta de esta envergadura los gerentes de cada una de las compañías indago, simulo y verifico que se lograran los objetivos esperados y que se lograra unir al mundo y renovar la confianza en las personas.' />
                </Grid.Row>
                <Grid.Row>
                    <Historiab titulo='CEO Kristan' imagen='historia/historia2.jpg' texto='Es el CEO de este megaproyecto tiene más de 10 años de experiencia en proyectos tecnológicos de éxito mundial. Junto con su equipo de trabajo es la persona idónea para esta gran responsabilidad de cambio.' />
                </Grid.Row>
                <Grid.Row>
                    <Historia titulo='AllVision ¿Cómo funciona?' imagen='https://img.europapress.es/fotoweb/fotonoticia_20171229171202_640.jpg' texto='Es una plataforma basada principalmente en la inteligencia artificial para el reconocimiento de imágenes, que permitirá identificar a cada uno de los individuos para agregar stast a el perfil según sus actividades realizadas y verificadas y para la consulta por cualquier ciudadano en cualquier momento solo utilizando su cámara celular u cualquier otra. Aún está en etapa de desarrollo, pero con la aprobación mundial y el presupuesto actual se espera revelar más detalles para los futuros usuarios.' />
                </Grid.Row>
            </Grid>
            </Fragment>
        );
    }
}

export default Historias;

