import React, { Component } from 'react';
import { Grid, Header, Container, Segment, List, Divider, Image, Button, Modal } from 'semantic-ui-react'

class Footer extends Component {




  render() {
    return (
      <Segment inverted vertical style={{ margin: '5em 0em 0em', padding: '5em 0em' }}>
        <Container textAlign='center'>
          <Grid divided inverted stackable>
            <Grid.Column width={3} floated="right">
              <Header inverted as='h4' content='Produccion Trasmedia' />
              <List link inverted>
                <List.Item as='a' href="https://sites.google.com/uao.edu.co/allvision/p%C3%A1gina-principal" >About AllVision</List.Item>
                <Modal trigger={<List.Item as='a'  >Productores</List.Item>}>
    <Modal.Header>Productores</Modal.Header>
    <Modal.Content image>
      <Image wrapped size='medium' src='staff/edwin.png' />
      <Modal.Description>
        <Header>Edwin Avila</Header>
        <p>Estudiante de Ingeniería Multimedia</p>
        <p>Universidad Autónoma de Occidente</p>
      </Modal.Description>
      <Image wrapped size='medium' src='staff/johan.png' />
      <Modal.Description>
        <Header>Johan Carvajal</Header>
        <p>Estudiante de Ingeniería Informática</p>
        <p>Universidad Autónoma de Occidente</p>
      </Modal.Description>
    </Modal.Content>
  </Modal>
                
              </List>
            </Grid.Column>
            <Grid.Column width={3} floated="left">
              <Header inverted as='h4' content='Comunidad' />
              <List link inverted>
                <Button circular color='facebook' icon='facebook' />
                <Button circular color='twitter' icon='twitter' />
                <Button circular color='youtube' icon='youtube' />
                <Button circular color='instagram' icon='instagram' />
              </List>
            </Grid.Column>
          </Grid>

          <Divider inverted section />
          <Image centered size='mini' src='https://img.europapress.es/fotoweb/fotonoticia_20171229171202_640.jpg' />
          <List horizontal inverted divided link size='small'>

            <List.Item as='a' href='#' onClick={()=>alert("en construccion")}>
              Contact Us
                </List.Item>
            <List.Item as='a' href='#' onClick={()=>alert("en construccion")}>
              Terms and Conditions
                </List.Item>
            <List.Item as='a' href='#' onClick={()=>alert("en construccion")}>
              Privacy Policy
                </List.Item>
          </List>
        </Container>
      </Segment>
    );
  }
}

export default Footer;