import React, { Component, Fragment } from 'react';
import FormLogin from '../login/formlogin';
import {  Header } from 'semantic-ui-react';
import * as am4core from "@amcharts/amcharts4/core";
import * as am4charts from "@amcharts/amcharts4/charts";
import am4themes_animated from "@amcharts/amcharts4/themes/animated";
import * as am4maps  from "@amcharts/amcharts4/maps";
import am4geodata_worldLow from "@amcharts/amcharts4-geodata/worldLow";
import am4geodata_continentsLow from "@amcharts/amcharts4-geodata/continentsLow";
am4core.useTheme(am4themes_animated);




class MapCenter extends Component {

    
    componentDidMount() {
        let chart = am4core.create("chartdiv", am4charts.XYChart);

        am4core.ready(function() {
    
            // Themes begin
            am4core.useTheme(am4themes_animated);
            // Themes end
            
            // Create map instance
            var chart = am4core.create("chartdiv", am4maps.MapChart);
            var interfaceColors = new am4core.InterfaceColorSet();
            
            try {
                chart.geodata = am4geodata_worldLow;
            }
            catch (e) {
                chart.raiseCriticalError(new Error("Map geodata could not be loaded. Please download the latest <a href=\"https://www.amcharts.com/download/download-v4/\">amcharts geodata</a> and extract its contents into the same directory as your amCharts files."));
            }
            
            
            var label = chart.createChild(am4core.Label)
            label.text = "Resultados de las votaciones a favor\nen porcentaje de los países\nparticipantes";
            label.fontSize = 12;
            label.align = "left";
            label.valign = "bottom"
            label.fill = am4core.color("#fff");
            label.background = new am4core.RoundedRectangle()
            label.background.cornerRadius(10,10,10,10);
            label.padding(10,10,10,10);
            label.marginLeft = 30;
            label.marginBottom = 30;
            label.background.strokeOpacity = 0.3;
            label.background.stroke =am4core.color("#927459");
            label.background.fill = am4core.color("#2131B2");//fondo
            label.background.fillOpacity = 0.6;
            
            var dataSource = chart.createChild(am4core.TextLink)
            dataSource.text = "EL MUNDO QUIERE ALLVISION";
            dataSource.fontSize = 12;
            dataSource.align = "left";
            dataSource.valign = "top"
            dataSource.urlTarget = "_blank";
            dataSource.fill = am4core.color("#FFF");//titulo
            dataSource.padding(10,10,10,10);
            dataSource.marginLeft = 30;
            dataSource.marginTop = 30;
            
            
        
        
            // Set projection
            chart.projection = new am4maps.projections.Orthographic();
            chart.panBehavior = "rotateLongLat";
            chart.padding(20,20,20,20);
            
            // Add zoom control
            chart.zoomControl = new am4maps.ZoomControl();
            
            var homeButton = new am4core.Button();
            homeButton.events.on("hit", function(){
              chart.goHome();
            });
            
            homeButton.icon = new am4core.Sprite();
            homeButton.padding(7, 5, 7, 5);
            homeButton.width = 30;
            homeButton.icon.path = "M16,8 L14,8 L14,16 L10,16 L10,10 L6,10 L6,16 L2,16 L2,8 L0,8 L8,0 L16,8 Z M16,8";
            homeButton.marginBottom = 10;
            homeButton.parent = chart.zoomControl;
            homeButton.insertBefore(chart.zoomControl.plusButton);
            
            chart.backgroundSeries.mapPolygons.template.polygon.fill = am4core.color("#000"); //mar
            chart.backgroundSeries.mapPolygons.template.polygon.fillOpacity = 1;
            chart.deltaLongitude = 20;
            chart.deltaLatitude = -20;
            
            
            // Create map polygon series
            
            var shadowPolygonSeries = chart.series.push(new am4maps.MapPolygonSeries());
            shadowPolygonSeries.geodata = am4geodata_continentsLow;
            
            try {
                shadowPolygonSeries.geodata = am4geodata_continentsLow;
            }
            catch (e) {
                shadowPolygonSeries.raiseCriticalError(new Error("Map geodata could not be loaded. Please download the latest <a href=\"https://www.amcharts.com/download/download-v4/\">amcharts geodata</a> and extract its contents into the same directory as your amCharts files."));
            }
            
            shadowPolygonSeries.useGeodata = true;
            shadowPolygonSeries.dx = 2;
            shadowPolygonSeries.dy = 2;
            shadowPolygonSeries.mapPolygons.template.fill = am4core.color("#fff");
            shadowPolygonSeries.mapPolygons.template.fillOpacity = 0.2;
            shadowPolygonSeries.mapPolygons.template.strokeOpacity = 0;
            shadowPolygonSeries.fillOpacity = 0.1;
            shadowPolygonSeries.fill = am4core.color("#fff");
            
            
            // Create map polygon series
            var polygonSeries = chart.series.push(new am4maps.MapPolygonSeries());
            polygonSeries.useGeodata = true;
            
            polygonSeries.calculateVisualCenter = true;
            polygonSeries.tooltip.background.fillOpacity = 0.2;
            polygonSeries.tooltip.background.cornerRadius = 20;
            
            var template = polygonSeries.mapPolygons.template;
            template.nonScalingStroke = true;
            template.fill = am4core.color("#040D5F"); //paises
            template.stroke = am4core.color("#DCFF05"); //lineas mapa
            
            polygonSeries.calculateVisualCenter = true;
            template.propertyFields.id = "id";
            template.tooltipPosition = "fixed";
            template.fillOpacity = 1;
            
            template.events.on("over", function (event) {
              if (event.target.dummyData) {
                event.target.dummyData.isHover = true;
              }
            })
            template.events.on("out", function (event) {
              if (event.target.dummyData) {
                event.target.dummyData.isHover = false;
              }
            })
            
            var hs = polygonSeries.mapPolygons.template.states.create("hover");
            hs.properties.fillOpacity = 1;
            hs.properties.fill = am4core.color("#deb7ad"); // los mapas seleccionado
            
            
            var graticuleSeries = chart.series.push(new am4maps.GraticuleSeries());
            graticuleSeries.mapLines.template.stroke = am4core.color("#fff");
            graticuleSeries.fitExtent = false;
            graticuleSeries.mapLines.template.strokeOpacity = 0.2;
            graticuleSeries.mapLines.template.stroke = am4core.color("#fff");
            
            
            var measelsSeries = chart.series.push(new am4maps.MapPolygonSeries())
            measelsSeries.tooltip.background.fillOpacity = 0;
            measelsSeries.tooltip.background.cornerRadius = 20;
            measelsSeries.tooltip.autoTextColor = false;
            measelsSeries.tooltip.label.fill = am4core.color("#fff");
            measelsSeries.tooltip.dy = -5;
            
            var measelTemplate = measelsSeries.mapPolygons.template;
            measelTemplate.fill = am4core.color("#FF0000"); //los puntos
            measelTemplate.strokeOpacity = 0;
            measelTemplate.fillOpacity = 0.75;
            measelTemplate.tooltipPosition = "fixed";
            
            
            
            var hs2 = measelsSeries.mapPolygons.template.states.create("hover");
            hs2.properties.fillOpacity = 1;
            hs2.properties.fill = am4core.color("#86240c");
            
            polygonSeries.events.on("inited", function () {
              polygonSeries.mapPolygons.each(function (mapPolygon) {
                var count = data[mapPolygon.id];
            
                if (count > 0) {
                  var polygon = measelsSeries.mapPolygons.create();
                  polygon.multiPolygon = am4maps.getCircle(mapPolygon.visualLongitude, mapPolygon.visualLatitude, Math.max(0.2, Math.log(count) * Math.LN10 / 10));
                  polygon.tooltipText = mapPolygon.dataItem.dataContext.name + ": " + count+"%";
                  mapPolygon.dummyData = polygon;
                  polygon.events.on("over", function () {
                    mapPolygon.isHover = true;
                  })
                  polygon.events.on("out", function () {
                    mapPolygon.isHover = false;
                  })
                }
                else {
                  mapPolygon.tooltipText = mapPolygon.dataItem.dataContext.name + ": no data";
                  mapPolygon.fillOpacity = 0.9;
                }
            
              })
            })
            
            
            var data = {
              "AL": 98,
              "AM": 80,
              "AO": 60,
              "AR": 70,
              "AT": 75,
              "AU": 55,
              "AZ": 48,
              "BA": 55,
              "BD": 57,
              "BE": 67,
              "BF": 68,
              "BG": 96,
              "BI": 77,
              "BJ": 76,
              "BR": 95,
              "BT": 93,
              "BY": 66,
              "CA": 67, 
              "CD": 71,
              "CF": 57,
              "CG": 97,
              "CH": 69,
              "CI": 41,
              "CL": 64,
              "CM": 67,
              "CN": 62,
              "CO": 84,
              "CY": 69,
              "CZ": 79,
              "DK": 58,
              "EE": 91,
              "EG": 63,
              "ES": 96,
              "FI": 73,
              "FR": 67,
              "GA": 73,
              "GB": 53,
              "GE": 89,
              "GH": 78,
              "GM": 69,
              "GN": 98,
              "GQ": 74,
              "GR": 54,
              "HR": 56,
              "HU": 74,
              "ID": 87,
              "IE": 56,
              "IL": 84,
              "IN": 85,
              "IQ": 96,
              "IR": 63,
              "IT": 79,
              "JP": 78,
              "KE": 68,
              "KG": 77,
              "KH": 64,
              "KM": 68,
              "KZ": 63,
              "LA": 93,
              "LK": 63,
              "LR": 92,
              "LS": 59,
              "LT": 64,
              "LU": 95,
              "LV": 69,
              "MA": 82,
              "MD": 83,
              "ME": 75,
              "MG": 86,
              "MK": 83,
              "ML": 68,
              "MM": 71,
              "MN": 66,
              "MR": 65,
              "MT": 85,
              "MV": 93,
              "MX": 64,
              "MY": 86,
              "MZ": 69,
              "NA": 92,
              "NE": 88,
              "NG": 54,
              "NL": 57,
              "NO": 57,
              "NP": 80,
              "NZ": 93,
              "PE": 79,
              "PK": 91,
              "PL": 82,
              "PT": 68,
              "RO": 63,
              "RS": 73,
              "RU": 64,
              "RW": 54,
              "SE": 64,
              "SG": 81,
              "SI": 47,
              "SK": 78,
              "SN": 47,
              "SO": 83,
              "SS": 93,
              "TD": 63,
              "TG": 84,
              "TH": 81,
              "TL": 46,
              "TN": 78,
              "TR": 70,
              "UA": 52,
              "UG": 55,
              "US": 96,
              "UZ": 95,
              "VE": 55,
              "ZA": 39,
              "ZM": 98,
              "ZW": 63
            }
            
            }); // end am4core.ready()
        this.chart = chart;
    }    

    render() {
        return (
            <Fragment >             
            <Header as='h4'  color='black'>
                EL MUNDO DIJO SI!
            </Header>  
            <div id="chartdiv"></div>
            
            </Fragment>

        );
    }
}

export default MapCenter;

