import React, { Component, Fragment } from 'react';
import {  Menu } from 'semantic-ui-react'

import Mapa from '../maps/globalmap';
import Noticias from '../noticias/noticias'
import Historias from '../historia/historias';
class NAVBAR extends Component {

    state = { activeItem: 'inicio' }

    handleItemClick = (e, { name }) => this.setState({ activeItem: name })
  

    contenido = () =>{


      switch(this.state.activeItem){
        case "inicio":
        return(
          <Mapa/>
        )
        case "historia":
        return(
          <Historias />
        )
        case "noticias":
        return(
          <Noticias/>
        )
        default:
        this.props.metodo(false);  

      } 

    }

    componentDidMount(){
      window.scrollTo(0, 0);
    }

    render() {
      const { activeItem } = this.state
  
      return (
  <Fragment>
          <Menu pointing secondary attached="top">
            <Menu.Item name='inicio' 
             active={activeItem === 'inicio'} 
             onClick={this.handleItemClick} 
            />
            <Menu.Item
              name='historia'
              active={activeItem === 'historia'}
              onClick={this.handleItemClick}
            />
            <Menu.Item
              name='noticias'
              active={activeItem === 'noticias'}
              onClick={this.handleItemClick}
            />
            <Menu.Menu position='right'>
              <Menu.Item
                name='salir'
                active={activeItem === 'salir'}
                onClick={this.handleItemClick}
              />
            </Menu.Menu>
          </Menu>
            {this.contenido()}
      </Fragment>
  
      )
    }
  }
  


export default NAVBAR;

