import React, {Component} from 'react';
import Formlogin from './components/login/formlogin'
import './App.css';
import 'semantic-ui-css/semantic.min.css'
import NavBar from './components/navbar/navbar'
import Footer from './components/footer/footer'

class App extends Component { 
  state = {
    login: false
  }

  login = (ok) =>{
    
    if(ok!=true){

      this.setState({
        login:false
      })
    }else{
      this.setState({
        login:true
      })
    }

  }

  componentesMain = () =>{
    if(this.state.login){
      return(
    <NavBar metodo={this.login}/> 
    
    
      )}
    else{
      return(
        <Formlogin metodo={this.login} />
      )
    }

  }

  componentDidMount(){
    window.scrollTo(0, 160);
  }
  
  render(){
  return (
    <div >
    {this.componentesMain()}
    <Footer />
    
    </div>
  )
}
}

export default App;
